
.Footer {
    height: 50px;
    background-color: white;
}

.Footer p {
    font-size: 12px;
    text-align: center;
    color: #555;
}

.particle {
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0;
    z-index: -1;
}

.landing {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.landing .Navbar {
  position: absolute;
  top: 0;
}

.landing .Navbar ul {
  list-style-type: none;
  margin: 40px;
  padding: 0;
}

.landing .Navbar ul li {
  display: inline;
  margin: 20px;
}

.landing .Navbar ul li a, .landing .Navbar ul li a:visited {
  text-transform: uppercase;
}

.landing .Navbar ul li a:hover, .landing .Navbar ul li a:visited:hover {
  text-transform: uppercase;
}

.landing .byline {
  margin-top: 4px;
}

.landing .info {
  display: block;
  text-align: center;

  background-color: transparent;

  margin: 0 auto;
  padding: 40px;

  -webkit-animation-duration: 1s;

          animation-duration: 1s;
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
}

.landing .info .Links a {
  margin: 5px;
  padding: 5px;
}

.landing .Profile-Pic {
  height: 150px;
  border-radius: 100px;
  margin: 0px auto;
  display: block;
}

@-webkit-keyframes fadeIn {
  0% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes fadeIn {
  0% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  height: 100vh;
  letter-spacing: 0.4px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1 {
  font-size: 32px;
  font-weight: 300;
  margin-top: 6px;
  margin-bottom: 4px;

}

a, a:visited {
  text-decoration: none;
  color: #282c34;
}

a:hover, a:visited:hover {
  color: #B9009A;
}

